import React, { Component } from 'react';
import classes from './UserPage.module.css';
import image1 from './pexels-kevin-ku-577585.jpg';

class UserPage extends Component {
    state = {
        firstName: "",
        lastName: "",
        phone: "",
        email: "",
        password: "",
        confirmPassword: ""
    } 

    postDataHandlder = () => {
        const data = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            phone: this.state.phone,
            email: this.state.email,
            password: this.state.password 
        }
        console.log(data);
    }


    render() {
        return(
            <div className={classes.UserPage}>
                <img className={classes.img} alt="coding picture" src={image1}/>
                <div className={classes.signDiv}>
                    <h1 className={classes.title}>Add a Post</h1>
                    <div className={classes.group}>
                        <input type="text" value={this.state.firstName} onChange={(event) => this.setState({firstName: event.target.value})} className={classes.input} placeholder="First Name"/>
                        <label className={classes.label}>First Name</label>
                    </div>
                    <div className={classes.group}>
                        <input type="text" value={this.state.lastName} onChange={(event) => this.setState({lastName: event.target.value})} className={classes.input} placeholder="Last Name"/>
                        <label className={classes.label}>Last Name</label>
                    </div>
                    <div className={classes.group}>
                        <input type="number" value={this.state.phone} onChange={(event) => this.setState({phone: event.target.value})} className={classes.input} placeholder="Phone Number"/>
                        <label className={classes.label}>Phone</label>
                    </div>
                    <div className={classes.group}>
                        <input type="email" className={classes.input} value={this.state.email} placeholder="Email Address" onChange={(event) => this.setState({email: event.target.value})} />
                        <label className={classes.label}>Email</label>
                    </div>
                    <div className={classes.group}>
                        <input type="password" placeholder="Password" className={classes.input} value={this.state.password} onChange={(event) => this.setState({password: event.target.value})} />
                        <label className={classes.label}>Password</label>
                    </div>
                    <div className={classes.group}>
                        <input type="password" className={classes.input} id="confirmPassword" placeholder="confirmPassword" value={this.state.ConfirmPassword} onChange={(event) => this.setState({ConfirmPassword: event.target.value})} />
                        <label for="confirmPassword" className={classes.label}>Confirm password</label>
                    </div>
                    <button className={classes.logBtn} onClick={this.postDataHandlder}>Sign In</button>
                </div>
            </div>
        );
    };
};

export default UserPage;