import React, { Component } from 'react';
import classes from './LoginPage.module.css';
import img1 from './pexels-negative-space-169573.jpg';

class LoginPage extends Component {


    render() {
        return(
            <div className={classes.loginPage}>
                <img className={classes.backImage} src={img1} alt="coding image" />
                <div className={classes.form}>
                    <div>
                        <h2 className={classes.title}>Log in now</h2>
                    </div>
                    <div class={classes.group}>
                        <input type="email" className={classes.input} placeholder="Email address" id="email" required/>
                        <label for="email" className={classes.label}>Email address</label>
                    </div>  
                    <div className={classes.group}>
                        <input type="password" className={classes.input} placeholder="Password" id="password" required/>
                        <label for="password" className={classes.label}>Password</label>
                    </div>
                    <div>
                        <button className={classes.logBtn}>Log In</button>
                    </div>
                </div>
            </div>
        );
    };
};

export default LoginPage;